.navbar {
    height: 56px;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #212124;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
}

.navbar nav {
    height: 100%;
}

.list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-flow: row;
    align-items: center;
    height: 100%;
}

.list-item {
    margin: 0;
    display: flex;
    height: 100%;
    width: auto;
    align-items: center;
    margin-right: 5px;
}

.list-itemb {
    margin: 0;
    display: flex;
    height: 100%;
    width: auto;
    align-items: center;
    
}

.list-item a {
    color: white;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
}

.list-item:hover {
    background-color: #808080;
    color: white;
    transition-duration: 0.5s;
}

.list-itemb:hover {
    background-color: #808080;
    color: white;
    transition-duration: 0.5s;
}
.movie-card {
  font: 14px/22px "Lato", Arial, sans-serif;
  color: #A9A8A3;
  padding: 40px 0;
}

.container {
  margin: 0 auto;
  width: 700px;
  height: 600px;
  background: #4d4d4d;
  border-radius: 5px;
  position: relative;
}



.details {
  padding: 50px 0 0 50px;
}

.title {
    color: white;
    font-size: 40px;
    margin-bottom: 13px;
    line-height: normal;
    position: relative;
}
.title span {
    margin-left: 12px;
    background: #C4AF3D;
    border-radius: 5px;
    color: #544C21;
    font-size: 14px;
    padding: 0px 4px;
  }
.description {
