.navbar {
    height: 56px;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #212124;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
}

.navbar nav {
    height: 100%;
}

.list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-flow: row;
    align-items: center;
    height: 100%;
}

.list-item {
    margin: 0;
    display: flex;
    height: 100%;
    width: auto;
    align-items: center;
    margin-right: 5px;
}

.list-itemb {
    margin: 0;
    display: flex;
    height: 100%;
    width: auto;
    align-items: center;
    
}

.list-item a {
    color: white;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
}

.list-item:hover {
    background-color: #808080;
    color: white;
    transition-duration: 0.5s;
}

.list-itemb:hover {
    background-color: #808080;
    color: white;
    transition-duration: 0.5s;
}